<template>
  <div>
    <headerBar :header-data="headerData" />
    <div class="container">

      <!-- 新增发票地址 begin -->
      <div class="content">
        <div class="common-main box">

          <van-tabs>
            <van-tab title="增值税专用发票">
              <div class="content common-main">
                <van-field
                  v-model="header"
                  v-validate="'required'"
                  required
                  name="fptt"
                  :error-message="errors.first('fptt')"
                  label="发票抬头"
                  label-width="110px"
                  placeholder="请输入发票抬头"
                />
                <div class="spe-list">
                  <div class="spe-left">
                    <span class="tit">发票内容</span>
                    <span class="info">
                      <van-radio-group v-model="invoicecontent">
                        <van-radio name="MX">明细</van-radio>
                        <van-radio name="DNPJ">电脑配件</van-radio>
                        <van-radio name="HC">耗材</van-radio>
                        <van-radio name="BGYP">办公用品</van-radio>
                      </van-radio-group>
                    </span>
                  </div>
                </div>
                <van-field
                  v-model="company_code"
                  v-validate="'required|sbmCheck'"
                  required
                  label="纳税人识别码"
                  label-width="110px"
                  name="nsrsbm"
                  :error-message="errors.first('nsrsbm')"
                  placeholder="请输入纳税人识别码"
                />
                <van-field
                  v-model="company_address"
                  v-validate="'required'"
                  required
                  label="注册地址"
                  label-width="110px"
                  name="zcdd"
                  :error-message="errors.first('zcdd')"
                  placeholder="请输入注册地址"
                />
                <van-field
                  v-model="company_phone"
                  v-validate="'required|TelOrMobile'"
                  required
                  type="number"
                  label="注册电话"
                  label-width="110px"
                  name="zcdh"
                  :error-message="errors.first('zcdh')"
                  placeholder="请输入注册电话"
                />
                <van-field
                  v-model="company_bank"
                  v-validate="'required'"
                  required
                  label="开户银行"
                  label-width="110px"
                  name="khyh"
                  :error-message="errors.first('khyh')"
                  placeholder="请输入开户银行"
                />
                <van-field
                  v-model="company_bankname"
                  v-validate="'required|yhzhCheck'"
                  required
                  label="银行账户"
                  label-width="110px"
                  name="yhzh"
                  :error-message="errors.first('yhzh')"
                  placeholder="请输入银行账户"
                />
                <van-field
                  v-model="address"
                  v-validate="'required|address'"
                  required
                  label="所在地区"
                  placeholder="请选择所在地区"
                  name="address"
                  label-width="110px"
                  is-link
                  readonly
                  :error-message="errors.first('address')"
                  @click="showPopup"
                />
                <van-field
                  v-model="invoiceaddress"
                  v-validate="'required'"
                  required
                  label="所在街道"
                  rows="2"
                  name="jtdz"
                  :error-message="errors.first('jtdz')"
                  autosize
                  type="textarea"
                  label-width="110px"
                  maxlength="50"
                  placeholder="请填写街道、楼道号等"
                />
              </div>
              <div class="bt-group">
                <van-button type="info" size="small" @click="saveInvoice('ZZSZYFP')">保存</van-button>
              </div>
            </van-tab>
            <van-tab title="普通发票">
              <div class="content common-main">
                <van-field
                  v-model="fptt1"
                  name="fptt1"
                  :error-message="message1"
                  label="发票抬头"
                  required
                  label-width="110px"
                  placeholder="请输入发票抬头"
                  @input="check1()"
                  @blur="check1()"
                />
                <div class="spe-list">
                  <div class="spe-left">
                    <span class="tit">发票内容</span>
                    <span class="info">
                      <van-radio-group v-model="fpnr">
                        <van-radio name="MX">明细</van-radio>
                        <van-radio name="DNPJ">电脑配件</van-radio>
                        <van-radio name="HC">耗材</van-radio>
                        <van-radio name="BGYP">办公用品</van-radio>
                      </van-radio-group>
                    </span>
                  </div>
                </div>
                <van-field
                  v-model="nsrsbm1"
                  label="纳税人识别码"
                  label-width="110px"
                  name="nsrsbm1"
                  required
                  :error-message="message2"
                  placeholder="请输入纳税人识别码"
                  @input="check2()"
                  @blur="check2()"
                />
                <!-- 识别码正确格式样例： 510202196202180320 -->
              </div>
              <div class="bt-group">
                <van-button type="info" size="small" @click="savePtInvoice('PTFP')">保存</van-button>
              </div>
            </van-tab>
          </van-tabs>

        </div>
      </div>

      <!-- 新增发票地址 end -->
    </div>
    <!-- 地址弹出层 beign -->
    <van-popup
      v-model="show"
      position="bottom"
      :style="{ height: '70%' }"
    >
      <!-- <van-area ref="myArea" :area-list="areaList" :value="addressShow" :columns-num="3" @change="onChange" @confirm="onConfirm" @cancel="onCancel" /> -->
      <XyAddress @getCheckResult="getCheckResult" />
    </van-popup>
    <!-- 地址弹出层 end -->
  </div>
</template>

<script land="script" scoped>
import headerBar from '@/components/header/headerBar.vue'
import XyAddress from '@/components/address/address.vue'
import { saveInvoice } from '@/store/module.js'
// import { pageRouter } from '@/common/js/mixin.js'
export default {
  name: 'InvoiceAdd',
  components: {
    headerBar,
    XyAddress
  },
  data() {
    return {
      addressShow: '', // 地区地址code码
      address: '', // 地区地址
      invoice: '', // 发票信息
      header: '', // 发票抬头
      company_code: '', // 纳税人识别码
      company_address: '', // 注册地址
      company_phone: '', // 注册电话
      company_bank: '', // 开户银行
      company_bankname: '', // 银行账户
      szjd: '', // 所在街道
      invoiceprovice: '', // 省份
      invoicecity: '', // 市
      invoicecounty: '', // 县
      invoicetown: '', // 区/镇
      invoiceaddress: '', // 详细地址
      checked: true,
      show: false,
      radio: '1',
      addressmodel: '',
      id: '',
      invoicecontent: 'MX', // 发票内容
      fptt: '',
      nsrsbm: '',
      fptt1: '',
      nsrsbm1: '',
      fpnr: 'MX',
      headerData: {
        title: '新增发票模板',
        leftUrl: 'back',
        leftText: ' '
      },
      message1: '',
      message2: ''
    }
  },
  created() {

  },
  methods: {
    check1() {
      if (this.fptt1) {
        this.message1 = ''
        return true
      } else {
        this.message1 = '请输入发票抬头'
        return false
      }
    },
    check2() {
      if (this.nsrsbm1) {
        if (!/^[A-Za-z0-9]+$/.test(this.nsrsbm1)) {
          this.message2 = '请输入正确的纳税人识别码'
          return false
        }
        this.message2 = ''
        return true
      } else {
        this.message2 = '请输入纳税人识别码'
        return false
      }
    },
    // 获取地址组件返回值
    getCheckResult(sheng, shi, xian, xiang, state) {
      if (!state) {
        this.show = false
      }
      if (sheng) {
        this.address = sheng.text
        this.invoiceprovice = sheng.value
      }
      if (shi) {
        this.address = this.address + '/' + shi.text
        this.invoicecity = shi.value
      }
      if (xian) {
        this.address = this.address + '/' + xian.text
        this.invoicecounty = xian.value
      }
      if (xiang) {
        this.address = this.address + '/' + xiang.text
        this.invoicetown = xiang.value
      }
    },
    saveInvoice(type) {
      this.$validator.validate().then(result => {
        if (result) {
          saveInvoice.data = {
            id: this.id,
            invoicetype: type,
            header: this.header,
            company_code: this.company_code,
            company_address: this.company_address,
            company_phone: this.company_phone,
            company_bank: this.company_bank,
            company_bankname: this.company_bankname,
            invoicecontent: this.invoicecontent,
            invoiceprovice: this.invoiceprovice ? this.invoiceprovice : '',
            invoicecity: this.invoicecity ? this.invoicecity : '',
            invoicecounty: this.invoicecounty ? this.invoicecounty : '',
            invoicetown: this.invoicetown ? this.invoicetown : '',
            invoiceaddress: this.invoiceaddress,
            jgbh: ''
          }
          this.asyncApiModule(saveInvoice).then(res => {
            this.$toast('保存发票信息成功')
            this.$router.back()
          })
            .catch(() => {
              this.loading = false
            })
        }
      })
    },
    savePtInvoice(type) {
      if (this.check1() & this.check2()) {
        saveInvoice.data = {
          id: this.id,
          invoicetype: type,
          header: this.fptt1,
          company_code: this.nsrsbm1,
          invoicecontent: this.fpnr,
          jgbh: ''
        }
        this.asyncApiModule(saveInvoice).then(res => {
          this.$toast('保存发票信息成功')
          this.$router.back()
        })
          .catch(() => {
            this.loading = false
          })
      }
    },
    goBack() {
      this.$router.back()
    },
    showPopup() {
      this.show = true
    },
    // value=0改变省，1改变市，2改变区
    onChange(picker, index, value) {
      const val = picker.getValues()
      console.log(val)// 查看打印
      let areaName = ''
      for (var i = 0; i < val.length; i++) {
        areaName = areaName + (i === 0 ? '' : '/') + val[i].name
      }
      this.addressmodel = areaName
    },
    // 确定选择城市
    onConfirm(val) {
      console.log('val-----', val)
      this.invoiceprovice = val[0].name
      this.invoicecity = val[1].name
      this.invoicecounty = val[2].name
      this.address = val[0].name + '/' + val[1].name + '/' + val[2].name
      this.addressShow = val[2].code
      this.show = false// 关闭弹框
    },
    // 取消选中城市
    onCancel() {
      this.show = false
      this.$refs.myArea.reset()// 重置城市列表
    }
  }
}
</script>

<style lang="css" scoped>
.bt-group .van-button{margin: 0;}
</style>
